body {
  background: linear-gradient(45deg, purple, blue, magenta, purple);
  background-size: 400% 400%;
  animation: gradientAnimation 15s ease infinite;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.countdown {
  text-align: center;
  color: white;
  font-size: 32px;
  margin-top: 50px;
  font-family: Arial, sans-serif;
}

.countdown-timer {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.countdown-item {
  margin: 0 10px;
  font-size: 48px;
}

.countdown-item p {
  margin-top: 10px;
  font-size: 24px;
}
